// Define Page Colour Variables
$peach: #fab96c;
$yellow: rgba(255, 209, 5, 0.65);
$purple: rgba(102, 45, 145, 0.65);
$lime: #e0e98f;
$pink: rgba(237, 28, 36, 0.65);
$grey: #3d3d3d;

form {
  width: 100%;
  margin: auto;
  margin-top: -1em;
  font-size: 1.6rem;
}

form label {
  font-size: 1rem;
}

/* Defines the styling of the 4 textboxes on the contact form */
input,
textarea {
  width: 90%;
  padding: 1rem;
  border-radius: 30px;
  outline: none;
  margin: 10px 0;
}

.contactData {
  background-color: rgb(196, 196, 196);
  border: none;
  margin: auto;
}

.contactCol {
  display: flex;
  margin: 0 1em 1em 1em;
  flex: 1;
  flex-basis: 40%;
}
.contact{
  h1{
    text-align: center;
  }
}

/* Define text color and font size inside the your message box on the contact page */
#elaborate {
  color: White;
  font-size: 1rem;
}

