.submitBtn {
  display: block;
  width: 114px;
  padding: 8px 20px;
  margin: 1em auto 1em auto;
  border-radius: 25px;
  outline: none;
  border: none;
  cursor: pointer;
  white-space: nowrap;
  font-weight: 900;
  background-color: $lime;
  color: $grey;
  border: 1px solid transparent;
  &:hover {
    color: $lime;
    background-color: $grey;
  }
}

@media screen and (min-width: 768px) {
  form {
    margin: 0;
    width: 70% !important;
  }
  .contactCol {
    display: flex;
    margin: 0 1em 1em 0;
    flex: 1;
    flex-basis: 40%;
  }
  input,
  textarea {
    width: 100%;
  }
  .contact{
    h1{
      text-align: inherit;
    }
  }
}
