.staff-card {
    background-color: #FAB96C;
    justify-content: center;
    text-align: center;
    min-width: 100%;
    padding-bottom: 1em;
}

.staff-card:nth-child(odd){
    background-color: #ffffff;
}

.staff-profile {
    /* margin: 1em 0 1em 0; */
    max-width: 200px;
    /* max-height: 300px; */
    border-radius: 50%;
    margin-top: 3em;
    margin-bottom: 1.5em;
    
}

.staffModal{
    text-align: center;
    width: 90%;
    margin: auto;
    margin-top: 6rem;
    background-image: linear-gradient(to top, #ffffff 70%, #FAB96C 30% ); 
    border: none;
    border-radius: 3%;
    position: relative;
    transition: all 0.5s ease;
    padding: 0.8em 0;
}

.modal-name{
    margin: 0;
}

.staffModal:focus{
    outline: none
}

.modalText{
    font-size: .7em;
    padding: 0 3em;
    text-align:justify        
}

.modal-profile {
    margin-top: 0.5em;
    max-width: 225px;
    max-height: 225px;
    border-radius: 50%;
}

.staffModalClose{
    position: absolute;
    right: 0.5em;
    top: 0.5em;
    font-size: 1.5em;
    color: #ffffff;
    cursor: pointer;
}

@media screen and (max-width: 560px) {
        .modal-profile {
            max-width: 150px;
            max-height: 150px
        }
        .staffModal {
            background-image: linear-gradient(to bottom, #FAB96C 125px, #ffffff 0% ); 
        }
}


@media screen and (min-width: 784px) {
    .staff-card {
        min-width: calc(100%/3) !important;
        background-color: inherit !important;
    }

    .staffModal{
        margin-top: 5.5em;
    }

    .staff-card:nth-child(7),.staff-card:nth-child(8) {
        min-width: 30% !important;
    }  

    .modalText{
        font-size: 0.8em
    }
}

@media screen and (min-width: 1000px) {
    .staffModal{
        max-width: 65%;
    }
}
@media screen and (min-width: 1300px) {
    .staffModal{
        max-width: 35%;
        margin-top: 8em;
    }
    .modalText{
        padding: 0 4em;
    }
}


