.newsTitle {
  text-align: center;
}

.responsiveCal {
  padding-bottom: 5%;
}

.responsiveCal iframe {
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  max-height: 300px;

}

.responsiveCal iframe span {
    font-size: 0.1em !important;
}


.upcomingWrapper{
  display: block;
  margin: auto;
}

.eventSection button {
  display: block;
  margin: auto;
}

@media screen and (min-width: 1000px) {
  .upcomingWrapper{
    display: flex;
  }
}

@media screen and (min-width: 800px) {
  .responsiveCal iframe {
    max-height: 500px
  }
}

form{width:100%;margin:auto;margin-top:-1em;font-size:1.6rem}form label{font-size:1rem}input,textarea{width:90%;padding:1rem;border-radius:30px;outline:none;margin:10px 0}.contactData{background-color:#c4c4c4;border:none;margin:auto}.contactCol{display:flex;margin:0 1em 1em 1em;flex:1 1;flex-basis:40%}.contact h1{text-align:center}#elaborate{color:White;font-size:1rem}.submitBtn{display:block;width:114px;padding:8px 20px;margin:1em auto 1em auto;border-radius:25px;outline:none;border:none;cursor:pointer;white-space:nowrap;font-weight:900;background-color:#e0e98f;color:#3d3d3d;border:1px solid transparent}.submitBtn:hover{color:#e0e98f;background-color:#3d3d3d}@media screen and (min-width: 768px){form{margin:0;width:70% !important}.contactCol{display:flex;margin:0 1em 1em 0;flex:1 1;flex-basis:40%}input,textarea{width:100%}.contact h1{text-align:inherit}}

header{text-align:center}header h1{margin-bottom:1rem}.searchbar{width:50%}.searchbar:focus{outline-width:0}

.staff-card {
    background-color: #FAB96C;
    justify-content: center;
    text-align: center;
    min-width: 100%;
    padding-bottom: 1em;
}

.staff-card:nth-child(odd){
    background-color: #ffffff;
}

.staff-profile {
    /* margin: 1em 0 1em 0; */
    max-width: 200px;
    /* max-height: 300px; */
    border-radius: 50%;
    margin-top: 3em;
    margin-bottom: 1.5em;
    
}

.staffModal{
    text-align: center;
    width: 90%;
    margin: auto;
    margin-top: 6rem;
    background-image: linear-gradient(to top, #ffffff 70%, #FAB96C 30% ); 
    border: none;
    border-radius: 3%;
    position: relative;
    transition: all 0.5s ease;
    padding: 0.8em 0;
}

.modal-name{
    margin: 0;
}

.staffModal:focus{
    outline: none
}

.modalText{
    font-size: .7em;
    padding: 0 3em;
    text-align:justify        
}

.modal-profile {
    margin-top: 0.5em;
    max-width: 225px;
    max-height: 225px;
    border-radius: 50%;
}

.staffModalClose{
    position: absolute;
    right: 0.5em;
    top: 0.5em;
    font-size: 1.5em;
    color: #ffffff;
    cursor: pointer;
}

@media screen and (max-width: 560px) {
        .modal-profile {
            max-width: 150px;
            max-height: 150px
        }
        .staffModal {
            background-image: linear-gradient(to bottom, #FAB96C 125px, #ffffff 0% ); 
        }
}


@media screen and (min-width: 784px) {
    .staff-card {
        min-width: calc(100%/3) !important;
        background-color: inherit !important;
    }

    .staffModal{
        margin-top: 5.5em;
    }

    .staff-card:nth-child(7),.staff-card:nth-child(8) {
        min-width: 30% !important;
    }  

    .modalText{
        font-size: 0.8em
    }
}

@media screen and (min-width: 1000px) {
    .staffModal{
        max-width: 65%;
    }
}
@media screen and (min-width: 1300px) {
    .staffModal{
        max-width: 35%;
        margin-top: 8em;
    }
    .modalText{
        padding: 0 4em;
    }
}



.staffDirectoryWrapper {
    /* margin-top: -3em; */
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.aboutStaff{
    text-align: center;
}

@media screen and (min-width: 784px) {
    .staffDirectoryWrapper{
        background-image: linear-gradient(to bottom, #ffffff 40%, #FAB96C 40% );
        background-size: 100% 379.66px;
        background-repeat: repeat;
        padding: 0em 5em 0 5em;
        transition: all 0.3s ease-out;
    }

    .aboutStaff{
        text-align: start;
        margin-left: 20%;
        transition: all 0.3s ease-out;
    }
}

@media screen and (min-width: 1000px) {
    .staffDirectoryWrapper{
        padding: 0em 12em 0 12em;
        transition: all 0.3s ease-out;
    }

    /* .aboutStaff{
        margin-left: 9em;
        transition: all 0.3s ease-out;
    } */
